*,
::after,
::before {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.2)
  );
  padding: 25px;
}

body {
  font-family: "montserrat", sans-serif;
  background-color: transparent;

  background-color: #ede0d4;

  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-content: center;
}

.App {
  border: none;
  border-radius: 20px;
  width: 400px;
  max-width: 90%;

  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  align-content: center;
  min-height: 80vh;
  margin: auto;
}

.container {
  text-align: center;

  padding: 1rem;
  margin: auto;
  color: white;

  width: 100%;
  visibility: visible;
  opacity: 1;
  transition: all 1s ease-in;
  background-image: radial-gradient(rgba(0, 0, 0, 0.2));
}

.container .not-show {
  visibility: hidden;
  opacity: 0;
  transition: all 1s ease-in;
}

.container > * {
  margin: 1rem;
}

.location-info,
.weather-info {
  margin: 1.5rem;
}

img {
  color: white;
}

.searchbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: rgba(4, 102, 200, 0.6);
  width: 100%;
  border-radius: 5px;
  padding: 1rem;
  color: #001233;
}

.input {
  margin: 10px 55px;

  display: block;
  width: 90%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 16px 0px 16px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.input:hover {
  background-color: rgba(255, 255, 255, 0.9);
}

.input:focus {
  border: none;
  box-shadow: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.9);
}

.city {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.degree {
  color: #fff;
  font-size: 100px;
  font-weight: 900;

  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.sky {
  margin-top: 5px;
  font-size: 20px;

  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.date {
  font-size: 20px;
  color: #fff;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.label {
  margin-bottom: 0;
}

.location-info {
  min-width: 100px;
  min-height: 100px;

  margin-inline: auto;
  border-radius: 20%;
  padding: 1rem;
}

.weather-icon {
  color: #fff;
  font-size: 100px;
  font-weight: 900;

  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 16px;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
